import RandomString from 'randomstring';
import { Record, fromJS } from 'immutable';

export default {
  record: Record({
    key: '',
    name: '',
    color: '',
    faction: ''
  }),

  makeOpenPlayer() {
    return this.record({ key: RandomString.generate({ length: 10 }) });
  },

  isOpen(player) {
    return player.name === '';
  },

  parse(object) {
    return this.record(fromJS(object));
  },
}