import Game from './game';

const NAV_LINKS = {
  NO_GAME: [
    { to: '/game', name: 'Start / Join Game' }
  ],
  SETUP: [
    { to: '/setup', name: 'Setup' },
    { to: '/overview', name: 'Overview' },
    { to: '/leave', name: 'Leave' }
  ],
  PLAYING: [
    { to: '/overview', name: 'Overview' },
    { to: '/objectives', name: 'Objectives' },
    { to: '/strategies', name: 'Strategies' },
    { to: '/leave', name: 'Leave' },
  ]
};

export default {
  linksForGame(game) {
    if (Game.isEmpty(game)) {
      return NAV_LINKS.NO_GAME;
    }

    switch (game.phase) {
      case Game.PHASES.SETUP:
        return NAV_LINKS.SETUP;
      case Game.PHASES.PLAYING:
        return NAV_LINKS.PLAYING;
    }
  },

  defaultPathForGame(game) {
    switch (game.phase) {
      case Game.PHASES.SETUP:
        return '/setup';
      case Game.PHASES.PLAYING:
        return '/overview';
    }
  }
}