import { List, Record, fromJS } from 'immutable';

const objective = Record({
  name: '',
  description: '',
  points: '',
  playerKeys: List(),
  addedFromAgenda: false
});

const STAGE_ONE = List([
  objective({ name: "Develop Weaponry", description: "Own 2 unit upgrade technologies." }),
  objective({ name: "Diversify Research", description: "Own 2 technologies in each of 2 colors." }),
  objective({ name: "Expand Borders", description: "Control 6 planets in non-home systems." }),
  objective({ name: "Intimidate Council", description: "Have 1 or more ships in 2 systems that are adjacent to Mecatol Rex's System." }),
  objective({ name: "Corner the Market", description: "Control 4 planets that each have the same planet trait." }),
  objective({ name: "Found Research Outposts", description: "Control 3 planets that have technology specialties." }),
  objective({ name: "Lead From the Front", description: "Spend a total of 3 tokens from your tactic and/or strategy pools." }),
  objective({ name: "Negotiate Trade Routes", description: "Spend 5 trade goods." }),
  objective({ name: "Sway the Council", description: "Spend 8 influence." }),
  objective({ name: "Erect a Monument", description: "Spend 8 resources." }),
]).map(o => (o.set('points', 1)));

const STAGE_TWO = List([
  objective({ name: "Revolutionize Warfare", description: "Own 3 unit upgrade technologies." }),
  objective({ name: "Master the Sciences", description: "Own 2 technologies in each of 4 colors." }),
  objective({ name: "Subdue the Galaxy", description: "Control 11 planets in non-home systems." }),
  objective({ name: "Conquer the Weak", description: "Control 1 planet that is in another player's home system." }),
  objective({ name: "Unify the Colonies", description: "Control 6 planets that each have the same planet trait." }),
  objective({ name: "Form Galactic Brain Trust", description: "Control 5 planets that have technology specialties." }),
  objective({ name: "Galvanize the People", description: "Spend a total of 6 tokens from your tactic and/or strategy pools." }),
  objective({ name: "Centralize Galactic Trade", description: "Spend 10 trade goods." }),
  objective({ name: "Manipulate Galactic Law", description: "Spend 16 influence." }),
  objective({ name: "Found a Golden Age", description: "Spend 16 resources." }),
]).map(o => (o.set('points', 2)));

export default {
  record: objective,
  MAX_OBJECTIVES: 10,

  getStartingObjectives() {
    return STAGE_ONE.sortBy(Math.random).take(2);
  },

  addNextObjective(currentObjectives) {
    if ( !this.canAddObjectives(currentObjectives) ) {
      return;
    }

    const standardObjectives = currentObjectives.filter(o => !o.addedFromAgenda);

    const nextObjectivePool = (standardObjectives.count() < 5 ? STAGE_ONE : STAGE_TWO)
      .filter(o => (
        currentObjectives.every(co => (co.name != o.name))
      ));

    const nextObjective = nextObjectivePool
      .sortBy(Math.random)
      .first();

    return currentObjectives.concat([nextObjective]);
  },

  addNextObjectiveFromAgenda(currentObjectives, stage) {
    const nextObjectivePool = (stage == 1 ? STAGE_ONE : STAGE_TWO)
      .filter(o => (
        currentObjectives.every(co => (co.name != o.name))
      ));

    const nextObjective = nextObjectivePool
      .sortBy(Math.random)
      .first()
      .set('addedFromAgenda', true);
    
    return currentObjectives.concat([nextObjective]);
  },

  canAddObjectives(currentObjectives) {
    return (
      currentObjectives
        .filter(o => !o.addedFromAgenda)
        .count() < this.MAX_OBJECTIVES
    );
  },

  parse(object) {
    return this.record(fromJS(object));
  },

  addPlayerToObjective(playerKey, objective) {
    return objective.set('playerKeys', objective.playerKeys.push(playerKey));
  },

  removePlayerFromObjective(playerKey, objective) {
    return objective.set('playerKeys', objective.playerKeys.filterNot(pk => (pk == playerKey)));
  }
};
