import { Record, List, fromJS } from 'immutable';
import Player from './player';
import Objective from './objective';

export default {
  PHASES: {
    SETUP: 'setup',
    PLAYING: 'playing'
  },

  record: Record({
    players: List(),
    phase: '',
    objectives: List()
  }),

  newGame() {
    return this.record({
      players: List.of(
        Player.makeOpenPlayer(),
        Player.makeOpenPlayer(),
        Player.makeOpenPlayer(),
        Player.makeOpenPlayer(),
        Player.makeOpenPlayer(),
        Player.makeOpenPlayer()
      ),
      phase: this.PHASES.SETUP,
      objectives: List([])
    });
  },

  parse(object) {
    const game = this.record(fromJS(object))
      .update('players', list => (
        list.map(p => (Player.parse(p)))
      ))
      .update('objectives', list => (
        list.map(o => (Objective.parse(o)))
      ));

    return game;
  },

  isEmpty(game) {
    return !game ||
      this.parse({}).equals(game);
  }
}