import * as Actions from './actions';
import { combineReducers } from 'redux';
import { connectRouter } from 'connected-react-router';

import Game from '../types/game';

function gameCode(state = '', action) {
  switch (action.type) {
    case Actions.CONNECT_TO_GAME:
      return action.gameCode;
    case Actions.RESET:
      return '';
    default:
      return state;
  };
}

function game(state, action) {
  const defaultState = state || Game.parse({});

  switch (action.type) {
    case Actions.UPDATE_GAME:
      return action.game;
    case Actions.RESET:
      return Game.parse({});
    default:
      return defaultState;
  };
}

export default (history) => combineReducers({
  router: connectRouter(history),
  gameCode,
  game
});