import io from 'socket.io-client';
import { push } from 'connected-react-router';
import Cookies from 'js-cookie';

import * as Actions from './ducks/actions';
import Game from './types/game';
import NavigationStates from './types/navigation-states';

const LAST_GAME_CODE_COOKIE = 'lastGameCode';

const Data = {
  socket: null,
  store: null,

  connectToGame(gameCode) {
    this.leaveGame();

    const newSocket = io.connect(window.location.origin);

    newSocket.on('connect', () => {

      newSocket.emit('subscribe', { gameCode }, (gameStateJSON) => {
        this.store.dispatch(Actions.connectToGame(gameCode));

        this.updateStoreFromServer(JSON.parse(gameStateJSON));
      });
    });

    newSocket.on('updateGame', (newState) => {
      this.updateStoreFromServer(newState);
    });

    this.socket = newSocket;
  },

  setStore(store) {
    this.store = store;
  },

  initializeGame() {
    this.socket.emit('publish', Game.newGame().toJS());
  },

  publishGame(gameFragment) {
    const newGame = this.store.getState().game.merge(gameFragment);

    this.socket.emit('publish', newGame.toJS());
  },

  updateStoreFromServer(gameFragment) {
    const game = Game.parse(gameFragment);

    if (Game.isEmpty(game)) {
      this.initializeGame();
      return;
    }

    const previousState = this.store.getState();

    this.store.dispatch(Actions.updateGame(game));

    const currentState = this.store.getState();

    if (previousState.game.phase != currentState.game.phase || previousState.gameCode != currentState.gameCode) {
      Cookies.set(LAST_GAME_CODE_COOKIE, previousState.gameCode, { expires: 3 });

      const targetUrl = NavigationStates.defaultPathForGame(game);

      this.store.dispatch(push(targetUrl));
    }
  },

  lastGameCode() {
    return Cookies.get(LAST_GAME_CODE_COOKIE)
  },

  leaveGame() {
    this.socket && this.socket.close();
    this.socket = null;

    this.store.dispatch(Actions.reset());
  }
}

export default Data;